<template>
    <div>
        <svg viewBox="0 0 200 200" class="progress-bar">
            <circle r="80" cx="100" cy="100" fill="none" stroke-width="12"
                stroke="var(--light-500)">
            </circle>

            <circle r="80" cx="100" cy="100" fill="none" stroke-width="12" stroke="url(#progress-bar-gradient)"
                stroke-linecap="round"
                :stroke-dasharray="`${ (data.value * 502.4 / 100).toFixed(1) } 502.4`"
                stroke-dashoffset="0" class="gradient"
                style="
                    transform: rotate(-90deg);
                    transform-origin: center;
                "></circle>

            <defs>
                <linearGradient id="progress-bar-gradient">
                    <stop offset="0%" stop-color="var(--secondary-500)"></stop>
                    <stop offset="100%" stop-color="var(--primary-500)"></stop>
                </linearGradient>
            </defs>
        </svg>

        <span v-html="props.title" v-if="props.title"></span>
    </div>
</template>

<script setup>
    import { reactive, watch } from 'vue';
    import { delay } from 'üîó/utils/utils';

    const props = defineProps({
        title: { type: String, default: '' },
        value: { type: Number, default: 0 }
    });

    const data = reactive({
        value: props.value
    });

    watch(
        () => props.value,
        async () => {
            let steps = 14;
            const letters = ((data.value).toString().split('.')[1] || '').length;
            const diff = +((props.value - data.value) / steps).toFixed(letters + 3);

            clearTimeout(data.timeout);

            const doIt = async () => {
                if (!steps) {
                    data.value = props.value;
                    return;
                }

                data.value += diff;
                steps -=1;

                clearTimeout(data.timeout);
                data.timeout = setTimeout(() => {
                    doIt();
                }, 10);
            };

            doIt();
        }
    );
</script>

<style lang="scss" scoped>
div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 240px;
    height: 240px;

    svg {
        width: 100%;
        height: 100%;
    }

    & > span {
        font: var(--font-body-2);
        font-weight: bold;
        color: var(--dark-500);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        text-align: center;
    }
}
</style>
