<template>
    <ul class="pagination" v-if="data.pages > 1">
        <li class="prev">
            <IconButton
                class="size-m"
                :disabled="data.currentPage === 1"
                icon="chevron-left"
                @click="prevPage(1)"
            />
        </li>
        <li
            v-for="page in pagination"
            :key="page"
            :title="page"
            @click.stop="props.isShort ? openDropdownSearch($event) : changePage(page)"
            :class="{
                active: !props.isShort && data.currentPage === page,
                'active-short': props.isShort && data.opened
            }"
        >
            <template v-if="page === 'prev'">
                <IconButton class="size-s" icon="more-horizontal" @click.stop="openDropdownSearch" />
            </template>

            <template v-else-if="page === 'next'">
                <IconButton class="size-s" @click.stop="openDropdownSearch" icon="more-horizontal" />
            </template>

            <template v-else>
                <span>{{ page }}</span>
            </template>
        </li>

        <li class="next">
            <IconButton
                class="size-m"
                :disabled="data.currentPage === data.pages"
                icon="chevron-right"
                @click="nextPage(1)"
            />
        </li>

        <Select
            v-if="props.showSizeChanger"
            class="noerror light"
            name="pageSize"
            :value="data.pageSize"
            :clearable="false"
            @change="changePageSize"
            :optionList="props.pageOptions"
        />

        <Teleport to="body">
            <span v-if="data.opened" class="modal-box-background" @click="closeDropdownSearch"></span>
            <div 
                v-show="data.opened" 
                class="search-page" 
                ref="searchPage" 
            >
                <Field
                    class="noerror light"
                    type="number"
                    name="searchStr"
                    @change="onSearchChange"
                    placeholder="10"
                    icon="search"
                />

                <ul>
                    <li
                        v-for="page in searchFilterPage"
                        :key="page"
                        :class="{
                            selected: data.currentPage == page,
                            matched: data.searchPage == page
                        }"
                        @click="changePage(+page)"
                    >
                        <span>
                            {{ page }}
                        </span>
                        <Icon icon="check" v-show="data.currentPage == page" />
                    </li>
                </ul>
            </div>
        </Teleport>
    </ul>
</template>

<script setup>
import { computed, nextTick, onMounted, reactive, ref, watch } from 'vue';

import Field from './Field.vue';
import IconButton from './IconButton.vue';
import Select from './Select.vue';
import { pageSizeOptions } from 'üîó/utils/utils';
const emit = defineEmits(['change']);

const props = defineProps({
    total: { type: Number, default: 1 },
    page: { type: Number, default: 1 },
    pageSize: { type: Number, default: 10 },
    showSizeChanger: { type: Boolean, default: false },
    step: { type: Number, default: 3 },
    maxShowPage: { type: Number, default: 10 },
    isShort: { type: Boolean, default: false },
    pageOptions: {
        type: Array,
        default: pageSizeOptions
    }
});

const searchPage = ref(null);

const data = reactive({
    pages: 1,
    currentPage: 1,
    pageSize: props.pageSize,
    opened: false,
    searchPage: null
});

const closeDropdownSearch = () => {
    data.opened = false;
};
const openDropdownSearch = (event) => {
    data.opened = true;
    const rect = event.target.getBoundingClientRect();
    nextTick(() => {
        if (rect.top + searchPage.value.offsetHeight + 10 >= window.innerHeight) {
            searchPage.value.style.top = 'auto';
            searchPage.value.style.bottom = `${searchPage.value.offsetHeight / 2 - 10}px`;
        } else {
            searchPage.value.style.top = `${rect.top + 10}px`;
            searchPage.value.style.bottom = 'auto';
        }
        searchPage.value.style.left = `${rect.right - searchPage.value.offsetWidth / 2}px`;
    });
};

let onSearchChangeTimeout = null;

const onSearchChange = (e) => {
    clearTimeout(onSearchChangeTimeout);
    onSearchChangeTimeout = setTimeout(() => {
        data.searchPage = e.target.value.trim();
    }, 200);
}

const searchFilterPage = computed(() => {
    const arr = Array(data.pages)
        .fill()
        .map((_, i) => `${i + 1}`);
    if (data.searchPage) {
        return arr.filter((el) => el.includes(data.searchPage));
    }
    return arr;
});

const getRange = (start, end) =>
    Array(end - start + 1)
        .fill()
        .map((_, i) => i + start);

const hideOverPages = (currentPage, pagesCount) => {
    if (data.pages <= props.maxShowPage) {
        return [...Array(data.pages)].map((_, i) => i + 1);
    }
    const isFirst = currentPage === 1;
    const isLast = currentPage === pagesCount;
    let delta = 0;
    if (pagesCount <= 2 + props.step) {
        delta = 2 + props.step;
    } else {
        delta += props.step;
        delta -= !isFirst + !isLast;
    }

    const range = {
        start: Math.round(currentPage - 1),
        end: Math.round(currentPage + 1)
    };

    if (range.start - 1 === 1 || range.end + 1 === pagesCount) {
        range.start += 1;
        range.end += 1;
    }

    let pages =
        currentPage > delta
            ? getRange(Math.min(range.start, pagesCount - delta), Math.min(range.end, pagesCount))
            : getRange(1, Math.min(pagesCount, delta + 1));

    const withDots = (value, pair) => (pages.length + 1 !== pagesCount ? pair : [value]);

    if (pages[0] !== 1) {
        const prevPages = [...Array(props.step)].map((_, i) => i + 1);
        if (pages.includes(prevPages[props.step - 1]) || pages[0] - prevPages[props.step - 1] === 1) {
            pages = Array.from(new Set([...prevPages, ...pages]));
        } else {
            pages = withDots(1, [...prevPages, 'prev']).concat(pages);
        }
    }

    if (pages[pages.length - 1] < pagesCount) {
        const nextPages = [...Array(props.step)].map((_, i) => pagesCount - props.step + i + 1);
        if (pages.includes(nextPages[0]) || nextPages[0] - pages[pages.length - 1] === 1) {
            pages = Array.from(new Set([...pages, ...nextPages]));
        } else {
            pages = pages.concat(withDots(pagesCount, ['next', ...nextPages]));
        }
    }

    return pages;
};

const pagination = computed(() => {
    if (data.pages) {
        return !props.isShort ? hideOverPages(data.currentPage, data.pages) : [data.currentPage];
    } else {
        return [];
    }
});

const changePageSize = ({ value }) => {
    data.pageSize = value;
    data.pages = Math.ceil(props.total / data.pageSize);
    if (data.pages < data.pageSize) {
        data.currentPage = data.pages;
    }

    emit('change', {
        page: data.currentPage,
        pageSize: data.pageSize
    });
};

const changePage = (page) => {
    data.opened = false;
    data.currentPage = page;
    emit('change', {
        page: data.currentPage,
        pageSize: data.pageSize
    });
};

const nextPage = (step) => {
    data.currentPage += step;
    emit('change', {
        page: data.currentPage,
        pageSize: data.pageSize
    });
};

const prevPage = (step) => {
    data.currentPage -= step;
    data.currentPage = data.currentPage === 0 ? 1 : data.currentPage;
    emit('change', {
        page: data.currentPage,
        pageSize: data.pageSize
    });
};

onMounted(() => {
    data.currentPage = props.page;
    data.pages = Math.ceil(props.total / data.pageSize);
});

watch(
    () => props.total,
    (total) => {
        data.pages = Math.ceil(total / data.pageSize);
    }
);
</script>

<style lang="scss" scoped>
.search-page {
    width: 160px;
    max-height: 186px;
    @include sub-bg;
    position: fixed;
    border-radius: 10px;
    padding: 12px;
    display: flex;
    flex-direction: column;
    gap: 18px;

    // TODO TEMP
    z-index: 400;

    ul {
        max-height: 102px;
        margin: 0;
        padding: 0;
        list-style: none;
        overflow-y: auto;
        @include scrollbar(var(--white-500), var(--dark-20), 0);

        li {
            padding: 2px 8px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 20px;
            cursor: pointer;
            font: var(--font-body-2);
            color: var(--dark-500);

            svg {
                fill: var(--primary-500);
                stroke: var(--tertiary-500);
                width: 16px;
                height: 16px;
            }

            &.selected {
                color: var(--primary-500);
                pointer-events: none;
            }

            &.matched {
                background-color: var(--primary-50);
                color: var(--primary-500);
            }
        }
    }
}

.pagination {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    list-style: none;
    box-sizing: border-box;
    margin: 16px 0;
    align-items: center;
    justify-content: start;
    padding: 0;

    &.center {
        justify-content: center;
    }

    & > div {
        max-width: 90px;
    }

    li {
        display: flex;
        align-items: center;
        justify-content: center;
        border-color: transparent;
        border-radius: 4px;
        background-color: transparent;
        min-width: 24px;
        height: 24px;
        font: var(--font-caption);
        span {
            color: var(--dark-80);
            user-select: none;
        }

        &.active {
            background-color: var(--primary-100);
            pointer-events: none;

            span {
                color: var(--primary-500);
            }
        }

        &.active-short {
            span {
                color: var(--dark-500);
            }
        }

        &:not(.next, .prev, .active, .all-pages) {
            &:hover {
                cursor: pointer;
            }
        }

        &.all-pages {
            span {
                color: var(--dark-20);
            }
        }
    }
}
</style>
