<template>
    <div @click.stop="" @mousedown="onMouseDown" class="range" ref="range" :class="{ disabled: props.disabled }">
        <span :style="{ width: pos }">
            <i></i>
        </span>
    </div>
</template>

<script setup>
import { reactive, computed, ref, onBeforeUnmount } from 'vue';
import stores from '@/stores';

const locale = stores.locale();
const emit = defineEmits(['change']);
const range = ref(null);

const props = defineProps({
    min: { type: Number, default: 0 },
    max: { type: Number, default: 100 },
    value: { type: Number, default: 0 },
    decimalCount: { type: Number, default: 0 },
    disabled: { type: Boolean, default: false }
});

const data = reactive({
    value: props.value,
    oldValue: props.value,
    pageX: 0,
    width: 1,
});

const updatePosition = (clientX) => {
    const rect = range.value.getBoundingClientRect();

    let relativeX = clientX - rect.left;
    if (locale.rtl) {
        relativeX = rect.right - clientX;
    }

    const percentage =  props.min + (props.max - props.min) * Math.max(0, Math.min(1, (relativeX / rect.width)))

    data.value = +percentage.toFixed(props.decimalCount)

    emit('change', data.value);
}

const onMouseUp = () => {
    window.removeEventListener('mousemove', onMouseMove)
    window.removeEventListener('mouseup', onMouseUp)
}

const onMouseMove = (e) => {
    updatePosition(e.clientX)
}

const onMouseDown = (e) => {
    if (props.disabled) {
        return;
    }

    updatePosition(e.clientX)

    window.addEventListener('mousemove', onMouseMove)
    window.addEventListener('mouseup', onMouseUp)
}


const pos = computed(() => {
    return (props.max - props.min) ? `${ 100 * (data.value - props.min) / (props.max - props.min) }%` : 0;
});

onBeforeUnmount(() => {
    window.removeEventListener('mousemove', onMouseMove)
    window.removeEventListener('mouseup', onMouseUp)
});
</script>

<style lang="scss" scoped>
    div {
        position: relative;
        min-width: 60px;
        height: 18px;
        display: flex;
        align-items: center;
        padding: 0 0;
        cursor: pointer;

        &:before {
            content: '';
            display: flex;
            height: 4px;
            background-color: var(--light-80);
            border-radius: 0;
            position: absolute;
            left: 9px;
            right: 9px;
            top: 50%;
            transform: translateY(-50%);
        }
        span {
            position: relative;
            height: 4px;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            background: var(--gradient-2);
            display: flex;
            i {
                right: 0;
                top: 50%;
                background: var(--gradient-2);
                position: absolute;
                transform: translateX(50%) translateY(-50%);
                width: 18px;
                height: 18px;
                border-radius: 50%;
                display: flex;
                flex-shrink: 0;
                cursor: e-resize;
            }
        }

        &.disabled {
            cursor: default;
            filter: grayscale(1);
            opacity: .6;
            span {
                i {
                    cursor: default;
                }
            }
        }
    }

    .direction-rtl .range {
        span {
            i {
                right: auto;
                left: 0;
                    transform: translateX(-50%) translateY(-50%);
            }
        }
    }
</style>
